import {mapState,mapMutations} from 'vuex';
export default {
	components: {
		
	},
	data() {
		return {
			addressList:[],
			cardId:'', 
			bankname:'',
			account:'',
			username:'',
			address:'',
			isDefault:false,
			bottomBtnText:'新增',
			
		}
	},
	computed: mapState(['token']),
	onShow(){
		// this.getShopcar();
	},
	onLoad(e) {
		if(e['cardId']) {
			var cardId = e['cardId'];
			this.cardId = cardId;
			this.bottomBtnText = '编辑';
			uni.setNavigationBarTitle({
				title: '编辑银行卡'
			})
		}
		this.getDetailCard(this.cardId);
	},
	methods: {
		getDetailCard(cardId){ //获取银行卡信息
			uni.request({
				url: this.websiteUrl + 'user/bank_detail',
				method: 'POST',
				data:{
					id:cardId
				},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("获取银行卡详情成功："+JSON.stringify(res.data));
						let data = res.data;
						let bankname = data.bankName;
						let account = data.bankNo;
						let username = data.bankUser;
						let address = data.bankAddress;
						let isDefault = data.typeState;
						this.bankname = bankname;
						this.account = account;
						this.username = username;
						this.address = address;
						this.isDefault = isDefault == 0?false:true;
						
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		modifyCard(){
			let id = this.cardId;
			let url;
			if(id){
				url = this.websiteUrl + 'user/bank_edit';
			}else {
				url = this.websiteUrl + 'user/bank_add';
				id = null;
			}
			let bankName = this.bankname;
			let bankNo = this.account;
			let bankUser = this.username;
			let bankAddress = this.address;
			let isDefault = this.isDefault == true?1:0;
			let data = {
				id:id,
				bankName:bankName,
				bankNo:bankNo,
				bankUser:bankUser,
				bankAddress:bankAddress,
				typeState:isDefault
			}
			console.log(data)
			if(!bankName || !bankNo || !bankUser || !bankAddress){
				uni.showToast({
					icon: 'none',
					title: '请先完善您的银行卡信息!',
					duration:2000
				});
				return false;
			}else {
				this.requestCardEdit(data,url);
			}
		},
		requestCardEdit(data,url){
			uni.request({
				url: url,
				method: 'POST',
				data:data,
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("银行卡编辑成功："+JSON.stringify(res));
						let msg = '银行卡新增成功!';
						if(this.cardId){
							msg = '银行卡修改成功!'
						}
						uni.showToast({
							icon: 'success',
							title: msg,
							duration:2000,
							success:function(){
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},2000)
							}
						});
						
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		setDefaultCard(){
			this.isDefault = !this.isDefault;
		}
		
	}
}
